.special-offer {
  @include e(subtitle) {
    font-size: 14px;
  }
  @include e(title) {
    font-size: 45px;
  }
}
.special-cats {
  @include e(box) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 550px;
    & li:nth-of-type(2) {
      display: block;
    }
  }
}
